import Vue from 'vue';
import Router from 'vue-router';

// async load component
const Home = () => import('@/pages/home');
const Share = () => import('@/pages/share');
const newWork = () => import('@/pages/new/work');
const newGenerate = () => import('@/pages/new/generate');

Vue.use(Router);

export default new Router({
  routes: [{
    path: '/',
    name: 'home',
    component: Home,
    beforeEnter(to, from, next) {
      if (localStorage.getItem('hasGenerate')) {
        next();
      } else {
        // next({
        //   path: '/generate',
        // })
        next();
      }
    }
  }, {
    path: '/share',
    name: 'share',
    component: Share
  }, {
    path: '/newWork',
    name: 'new-work',
    component: newWork,
  }, {
    path: '/generate',
    name: 'generate',
    component: newGenerate,
  }],
});



