/* eslint-disable no-param-reassign,no-plusplus,import/prefer-default-export */

export function getValueByPath(obj, path, defaultVal) {
  path = path.replace('[', '.').replace(']', '');
  path = path.replace(/^\./, '').replace(/\.$/, '');
  const pathArr = path.split('.');
  let value = obj;

  try {
    for (let i = 0, len = pathArr.length; i < len; i++) {
      const key = pathArr[i];
      value = value[key];
    }
  } catch (e) {
    value = defaultVal;
  }

  if (typeof value === 'undefined') {
    value = defaultVal;
  }

  return value;
}

export default {};



