/* eslint-disable no-shadow */

import {
  getValueByPath,
} from '@/assets/js/utils';

const state = {
  list: [{
    name: 'DH',
    startTime: '2018-01',
    endTime: '至今',
    job: '前端开发',
    detail: '',
    logo: '/resume-card/static/image/dh.jpg',
  }]
};

const getters = {
  firstWork(state) {
    return getValueByPath(state.list, '[0]', {});
  }
};

export default {
  namespaced: true,
  state,
  getters,
};



